.grid-container {
  width: 90%;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-auto-rows: 100px;
  grid-gap: 2em;
  margin: 0 auto;
}

.masthead {
  height: 50vh;
  grid-column: 1 / -1;
  grid-row: 1 / 1;
  background-image: url("../images/tea-ceremony.jpg");
  background-position: 60% 40%;
}

.title {
  grid-column: 1 / 6;
  grid-row: 1 / 1;
  z-index: 10;
  color: white;
  margin-left: 1em;
}

@supports (writing-mode: sideways-lr) {
  .title {
    color: #444;
    grid-column: 1 / 1;
    grid-row: 5 / 10;
    text-align: end;
    writing-mode: sideways-lr;
  }
}

.content {
  grid-column: 2 / 6;
  grid-row: 4;
  background-color: #fbfbf4;
  padding: 0 1em 1em 1em;
}

.content blockquote {
  color: #4a2512;
  clear: both;
  font-size: calc(var(--p-font-size-min) * 1.5em);
  line-height: calc(var(--p-line-height-min) * 1.125em);
}

.content ul li {
  font-size: 1rem;
}
